.main-menu {
    display: inline-flex;
	margin: 0;
	padding: 0;
    &__list {
        list-style: none;
        padding: 20px 26px 33px 0;
        position: relative;
        & > a {
            font-size: 18px;
            line-height: 140%;
            color: $grey-1;
            margin-right: 24px;
            &::after {
                content: "";
                background-image: url("../img/down_arrow.svg");
                width: 24px;
                height: 24px;
                display: inline-block;
                vertical-align: middle;
            }
        }
    }
    &__list:hover{
            a{
                &::after {
                    transform: rotate(180deg);
                    transition-duration: 0.4s;
                    transition-property: transform;
                }
            }
    }
    &__list:hover {
        & > a {
            color: $primary-purple;
            transition-duration: 0.4s;
            transition-property: color;
        }
    }
    &__list:hover {
        & .main-menu__sub {
            display: block;
            transition-duration: 0.4s;
            transition-property: display;
        }
    }
    &__sub {
        box-shadow: 0px 14px 32px rgba(0, 0, 0, 0.11), inset 0px 0px 1px #C5C5C5;
        border-radius: 8px;
        background: $white;
        padding: 16px 0 0 16px;
        width: 168px;
        height: 167px;
        position: absolute;
        z-index: 2;
        top: 65px;
        display: none;
        & li {
            list-style: none;
	        padding-bottom: 8px;
        }
        & a {
            font-size: 16px;
            line-height: 19px;
            color: $grey-2;
            &:hover {
                color: $grey-1;
                text-decoration: underline;
                transition-duration: 0.4s;
                transition-property: color, text-decoration;
            }
        }
    }
}