.arrow {
	width: 35px;
	height: 35px;
	border-radius: 50%;
	border: none;
	background-color: $white-transparency;
	background-image: url("../img/arrow_left.svg");
	background-repeat: no-repeat;
	background-position: center center;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
}